.DreamKitchen{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60vh !important  ;
    flex-direction: column;
    
}

.DreamKitchen h1 {
    font-size: 50px;
    margin-bottom: 50px;
    text-align: center;
    font-weight: 700;
    letter-spacing: 2px;
    color: #CC8A3E;
}
.DreamKitchen button {
    padding: 15px 100px;
    text-transform: uppercase;
    font-size: 13px;
    background: none;
    font-weight: 600;
    transition: .3s ease-in-out;
    border: 2px solid #CC8A3E;
    color: #CC8A3E;
}
.DreamKitchen button:hover{
    background: #CC8A3E;
    color: white;
}

.about-us.need-space h6 {
    border-bottom: 1px solid white;
    padding-top: 15px;
}