.content h1 {
    font-weight: 500;
    font-size: 57px;
}

.content h1 span{
    font-style: italic;
}

.content h3{
    font-style: italic;
    margin-top: 10px;
}

.content button {
    background: none;
    padding: 22px 23px !important;
    font-size: 10px;
    color: #ffffff;
    margin-top: 20px;
    position: relative;
    background: #CC8A3F;
    border: none;
    width: 230px;
    text-align: left;
    font-weight: 500;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #CC8A3F;
    text-transform: uppercase;
    letter-spacing: 1px;
    transition: .3s ease-in-out;
}
.content button svg{
    font-size: 20px;
}

.content button:hover{
    background: none;
    color: #CC8A3F;
}

.content{
    display: flex;
    flex-direction: column;
    justify-content: end;
    height: 100%;
}


.carousel-indicators [data-bs-target] {
    box-sizing: content-box;
    flex: 0 1 auto;
    width: 24px;
    height: 3px;
    padding: 0;
    margin-right: 7px;
    margin-left: 7px;
    text-indent: -999px;
    cursor: pointer;
    background-color: #000000;
    background-clip: padding-box;
    border: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    transition: opacity .6s ease;
    padding: 8px 0; transition: .3s ease-in-out;
  
}

.carousel-indicators [data-bs-target]:hover{
    background-color: #000000 !important;
    opacity: 1 !important;
}

a{
    text-decoration: none;
}

.visualize {
   
    height: 60vh !important
    
}

.visualize-btn-group {
    margin-top: 50px !important;
    display: flex;
}

.DreamKitchen {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70vh;
    flex-direction: column;
}

.carousel-indicators {
    position: absolute;
    right: 0;
    bottom: 75px !important;
    left: 0;
    z-index: 2;
    display: flex;
    justify-content: center;
    padding: 0;
    margin-right: 15%;
    margin-bottom: 1rem;
    margin-left: 15%;
    list-style: none;
}

.content h2 {
